@import url(https://fonts.googleapis.com/css2?family=KoHo&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Newsreader&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Reggae+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Padauk&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: 'Padauk', sans-serif;
}
/* Navbar */
nav {
  z-index: 30;
}
.navbar-brand {
  font-size: 1.3rem !important;
  font-family: 'Reggae One', cursive;
}
.navbar-nav .nav-item .nav-link {
  letter-spacing: 0.0375rem;
  font-weight: 500;
  cursor: pointer;
}
nav ul li .nav-link {
  text-transform: uppercase;
  margin-left: 0.75rem;
  margin-right: 0.75rem;
  border-top: 0.125rem solid rgba(255, 255, 255, 0);
}
nav ul li .nav-link.active, nav ul li .nav-link.active:focus {
  border-top: 0.125rem solid #F46036;
}
nav ul li .nav-link:hover {
  border-top: 0.125rem solid #F46036;
}
/* Header */
.header-wrapper {
  position: relative;
  background: url('https://res.cloudinary.com/rokuirie/image/upload/v1621370073/Portfolio/quentin-schulz-sQ74rgQsPvI-unsplash_1_byrjtb_1_imt6km.jpg') no-repeat;
  background-position: center;
  background-size: cover;
  height: 100vh;
}
.main-info {
  display: -webkit-flex;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  color: #fff;
  z-index: 1;
}
.main-info h1 {
  text-transform: uppercase;
  font-family: 'Newsreader', serif;
  color: #F46036;
  margin-top: 2rem;
  font-size: 2.5rem !important;
}
.main-info .typed-text {
  font-size: 1.25rem;
}
.main-btn {
  border: 2px solid #F46036;
  border-radius: 0;
  padding: 0.625rem 0.75rem;
  color: #fff;
  margin-top: 2rem;
  text-transform: uppercase;
}
.main-btn:hover {
  background-color: #F46036;
  color: #fff;
  text-decoration: none;
  transition: 0.2s ease-in-out;
}
.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
}
/* About section */
.about {
  background-color:  #2e2d2e;
}
.about h1 {
  font-size: 2.5rem !important;
  padding-top: 1.875rem;
  color: #F46036;
  font-family: 'Newsreader', serif;
  text-transform: uppercase;
}
.about .row {
  margin-top: 5vh;
  color: whitesmoke;
  font-size: 1.25rem;
}
.about .row img {
  width: 100%;
}
/* Services */
.services {
  background-color: whitesmoke;
  /* height: 100vh; */
}
.services h1 {
  padding-top: 1.875rem;
  font-size: 2.5rem !important;
  text-transform: uppercase;
  font-family: 'Newsreader', serif;
  color: #F46036;
}
.services .row {
  margin-top: 5vh;
}
.services .service-card {
  margin-top: 3.125rem;
  margin: 0 auto;
  float: none;
  cursor: pointer;
}
.services .card-title {
  text-transform: uppercase;
}
/* Projects */
.projects {
  background-color:  #2e2d2e;
  /* height: 129vh; */
}
.projects h1 {
  font-size: 2.5rem !important;
  padding-top: 1.875rem;
  color: #F46036;
  font-family: 'Newsreader', serif;
  text-transform: uppercase;
}
.projects .row {
  margin-top: 5vh;
}
.projects .card {
  margin-bottom: 0.8rem;
}
.projects .btn {
  background-color: #F46036;
  color: #fff;
  border: 2px solid #fff;
  margin-bottom: 1rem;
}
.projects .btn:hover {
  color: #F46036;
  background-color: #fff;
  border: 2px solid #F46036;
  margin-bottom: 1rem;
}
.projects .card-title {
  color: #F46036;
}
.contact {
  background-image: url("https://res.cloudinary.com/rokuirie/image/upload/v1621925748/Portfolio/bekky-bekks-gk-2lMtCeR8-unsplash_1_rhfwwf.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.contact h1 {
  font-size: 2.5rem !important;
  padding-top: 2.5rem;
  color: #F46036;
  font-family: 'Newsreader', serif;
  text-transform: uppercase;
}
.form-control {
  border: 1.8px solid #2e2d2e;
  border-radius: 0.25rem !important;
  padding: 0.625rem !important;
}
.contact .form .btn {
  background-color: #F46036;
  color: white;
  text-align: center;
  width: 10rem;
  border: 2px solid #F46036;
  margin-bottom: 2rem;
}
.contact .form .btn:hover {
  color: #F46036;
  background-color: white;
  text-align: center;
  /* width: 5rem; */
  border: 2px solid #F46036;
  margin-bottom: 2rem;
}
.error {
  position: absolute;
  color: red;
  font-size: 0.8rem;
}

@media (max-width: 768px) {
  nav .navbar-brand {
    font-size: 0.95rem !important;
  }
  nav ul li .nav-link {
    font-size: 0.85rem !important;
  }
  .particles-canvas {
    display: none;
  }
  .typed-text {
    font-size: 1.3rem;
  }
  .main-info h1 {
    font-size: 1.8rem !important;
  }
  .about .row {
    margin-top: 5vh;
  }
  .about h1 {
    font-size: 1.8rem !important;
  }
  .about .padding {
    padding: 0.625rem;
    padding-top: 1.875rem;
    padding-bottom: 1.875rem;
  }
  .about .col-md-7 {
    margin-top: 0.625rem;
    font-size: 0.95rem !important;
  }
  .services h1 {
    font-size: 1.8rem !important;
  }
  .services .service-card .card {
    margin-left: 0 auto;
    float: none;
  }
  .projects h1 {
    font-size: 1.8rem !important;
  }
  .projects .card img {
    display: none;
  }
  .contact {
    background-image: none;
  }
  .contact h1 {
    font-size: 1.8rem !important;
  }
  .form label {
    font-size: 0.95rem;
  }
}
@media (min-width: 992px) {
  .padding {
    padding: 3.75rem;
  }
  .services .service-card .card .card-body {
    width: 18.75rem !important;
    height: 15.625rem !important;
    font-size: 1.125rem;
  }
  .services .service-card {
    margin-top: 3.125rem;
    margin: 0 auto;
    float: none;
    margin-bottom: 5rem;
    cursor: pointer;
  }
  .material-icons {
    font-size: 2.5rem;
  }
  .services .service-card .card {
    transition: -webkit-transform 0.35s;
    transition: transform 0.35s;
    transition: transform 0.35s, -webkit-transform 0.35s;
  }
  .services .service-card .card:hover {
    -webkit-transform: scale(1.025);
            transform: scale(1.025);
    border: 2px solid #2e2d2e !important;
    background-color: whitesmoke !important;
    color: #2e2d2e !important;
  }
  .projects .card {
    border: 2px solid #2e2d2e;
    cursor: pointer;
  }
  .projects .card:hover {
    border: 2px solid #fff;
  }
  .form label {
    font-size: 1.3rem;
  }

}
